    this.$store.dispatch(SET_BREADCRUMB, [{ title: "Report" }]);
    // ApiService.get("jumlah-kuantitas").then(rs => {
    //   this.items = rs.data.data;
    // });
  },
  methods: {
    total(index = "b") {
      var sum = 0;
      this.items.forEach(e => {
        sum += e[index];
      });
      return sum;
    }
  },
  computed: {
    rows() {
      return this.items.length;
    }
  }
};
